.mynavbar{
    margin: 0 auto;
}
.nav-link{
    width: 100px;
    color: #fff;
}
.navbarColor{
    background-color: #cf8c11d9;
    font-weight: bold;
}
hr{
    color:#000; background-color:#000; margin: 0 auto 0 0;
}
.navbar-light .navbar-nav .nav-link:hover {
    color: #fff;
    background-color: rgb(58, 51, 31);
    border-radius: 25px;
}